<template>
    <div>
        <mhead-top titleHead="我的收藏" lfIcon="el-icon-arrow-left icon" lfUrl="/userCenter/index/index" rgIcon="el-icon-s-home icon" rgUrl="/home"></mhead-top>
        <div class="mBg">
            <div class="mCBox" >
                <div v-show="list.length==0"  class="f16 color6 mt30 tc w">
                    暂无收藏
                </div>
                <div v-show="list.length!=0" class="fbox">
                    <div class="mItem" v-for="(item,index) in list" :key="index">
                        <div class="tc" @click="toDetail(item.spec_id)">
                            <el-image class="mImg" :src="item.list_pic_url" fit="cover"></el-image>
                        </div>
                        <div class="mName wto f14" @click="toDetail(item.spec_id)">{{item.good_name}}</div>
<!--                        <div class="color9">规格：{{item.spec_name}}{{item.spec_unit}}</div>-->
                        <div class="flex f-nowrap j-bet">
                            <div class="mPrice">{{item.price_type}}{{item.price_cn}}<span class="f14">/{{item.supply_unit}}</span></div>
                            <div @click="delCollect(item.collect_id)"><i class="el-icon-delete icon"></i></div>
                        </div>
                    </div>
                    <div class="page">
                        <el-pagination
                                small
                                class="tc mt10"
                                @current-change="handleCurrentChange"
                                hide-on-single-page
                                prev-text="上一页"
                                next-text="下一页"
                                layout="prev, pager, next"
                                :current-page="page"
                                :page-size="rows"
                                :pager-count="5"
                                :total="count">
                        </el-pagination>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>


<script>
    import {getMyCollection,delMyCollection} from '../../../service/api'
    import mheadTop from '../../../components/mobile/headTop';
    import collectscroll from '../../../components/common/collectScroll';
    import BScroll from 'better-scroll'

    export default {
        name: 'mCollect',
        data(){
            return{
                list:[],
                rows:12,//每页显示数
                page:1,//当前页面
                count:0,//总条数
                totalPage:'',//总页数
            }
        },
        components:{
            mheadTop,
        },
        computed:{
        },
        created(){
            this.initData();
        },
        methods:{
            async initData(){
                var res = await getMyCollection(
                    this.rows,
                    this.page
                );
                if(res){
                    if(res.status != 12007){
                        this.list = res.data;
                        this.count = res.count;
                        this.totalPage = Math.ceil(this.count/this.rows);
                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                    }
                }
            },
            async delCollect(collect_id){
                this.$confirm('你确定要取消该收藏吗?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    let res = await delMyCollection(collect_id);
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.initData();
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {

                });
            },
            toDetail(specifications_id){
                this.$router.push({path:"/good/detail",query:{specifications_id:specifications_id}});
            },
            handleCurrentChange(val) {
                //改变页码
                this.page = val;
                this.initData();
            }

        },


    }

</script>

<style lang="scss">
    @import '../../../style/mixin';
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .wrapper{
        overflow: hidden;
    }
    .mCBox{
        background-color: $bggray;
        padding:0;
        margin:0;
    }
    .mItem{
        background-color: #fff;
        border-radius:3px;
        width:px2rem(300);
        padding:px2rem(20);
        display: inline-block;
        margin-bottom:10px;
    }
    .mItem:nth-child(even){
        margin-left:px2rem(20) ;
    }
    .mImg{
        width:px2rem(300);
        height:px2rem(250);
    }
    .mSize{
        font-size:px2rem(22);
        color:#999;
    }
    .mName{
        font-size:px2rem(28);
        color:#333;
        height: px2rem(48);
    }
    .mPrice{
        font-size:px2rem(26);
        color:$colorprice;
    }
    .el-icon-delete.icon{
        color:#999;
        font-size: px2rem(30);
    }

    .fbox{
        width: 100%;
        display:flex;
        padding:px2rem(20);
        display: flex;
        flex-wrap: wrap;
    }
    .page{
        width:100%;
        height: px2rem(104);
        position: fixed;
        bottom:0;
        left:0;
        background-color:$colorwhite;
        border-top:1px solid #eee;
        z-index: 1000;
    }

</style>



